.nav {
  font-weight: 600;
  @media (max-width: 983px) {
    display: none;
  }

  &__list {
    display: flex;
    @media (max-width: 983px) {
      flex-direction: column;
    }
  }
  &__item {
    &:not(:last-child) {
      margin-right: 3.5rem;
      @media (max-width: 1260px) {
        margin-right: 2.5rem;
      }
    }
    transition: var(--transition);
    &:hover {
      color: var(--color-violet);
    }
  }
}


