.quote{
    position: relative;
    margin-bottom: 16.5rem;
    &__inner { 
        padding: 50px 80px;
        border-radius: 1.8rem 0;
        border: 1px solid var(--color-white);
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-gap: 3.7rem;

    }
    &__text{ 
        font-weight: 400;
        max-width: 32.5rem;
        width: 100%;
        color: var(--color-white);
        padding-bottom: 1.5625rem;
        position: relative;
        font-size: 1.1rem;

        &::after{
            position: absolute;
            content: '';
            width: 100%;
            height: 1px;
            background-color: var(--color-white);
            bottom: 0;
            left: 0;
        }
    }
    &__link{
        display: block;
        margin-top: 1.5625rem;
        color: var(--color-violet);
        font-weight: 700;
    }

    &__img{
        img { 
            position: absolute;
            max-width: 370px;
            border-radius: 1.8rem 0;
            top: -50px;
            right: 450px;
        }
    }
}